import React, { useState } from 'react';
import fileIcon from "../Images/file-solid.svg";
import uploadIcon from "../Images/upload-solid.svg";
import axios from "axios";

export default function Home() {
    const [file, setFile] = useState();

  const uploadFile = async () => {
    var formData = new FormData();
    formData.append("file", file);
    await axios.post("http://localhost:8000/upload", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      }
  })
    .then((res)=>{console.log(res.data)})
    .catch((err)=>{console.log(err.message)});
  }  

  const fileSize = () => {
    if (file.size >= 1073741824) {
      return (
        <p className='text-md text-red-600 font-semibold'>
          {(file.size / 1073741824).toFixed(2)} GB
        </p>
      );
    } else if (file.size <= 52428800 && file.size >= 1048576) {
      return <p className='text-md text-white font-semibold'>{(file.size / 1048576).toFixed(2)} MB</p>;
    } else if (file.size > 52428800) {
      return (
        <p className='text-md text-red-600 font-semibold'>
          {(file.size / 1048576).toFixed(2)} MB
        </p>
      );
    } else if (file.size >= 1024) {
      return <p className='text-md text-white font-semibold'>{(file.size / 1024).toFixed(2)} KB</p>;
    }
  }
  return (
    <div className='w-[100%] flex flex-col justify-center items-center' data-aos="fade-up" data-aos-duration="1000">
        <div className='w-[100%] lg:w-fit flex flex-col'>
          <h1 className='text-white mb-4 ml-2 lg:ml-0'>Upload your result as a PDF file:</h1>
          <div className='w-[95%] lg:w-[700px] h-[150px] border-[1px] rounded border-gray-700 flex flex-col justify-center items-center mb-4 self-center'>
            <div onClick={()=>document.getElementById("fileInput").click()} htmlFor="fileInput" className='flex flex-row items-center justify-center bg-blue-600 hover:bg-blue-500 rounded w-[200px] h-[50px] cursor-pointer mb-2'>
              <input type="file" accept='.pdf' id="fileInput" className='hidden' onChange={(e) => {setFile(e.target.files[0])}}/>
              <label className='inline text-white font-semibold cursor-pointer'><img className='w-4 h-4 inline mr-2' src={fileIcon} alt="file"></img>Choose a file</label>
            </div>
            <span className='text-white'><p className='text-md font-semibold'>{file!==undefined?file.name:""}</p></span>
            <span className='text-white'>{file!==undefined?fileSize():null}</span>
          </div>
          <button className='w-[95%] lg:w-[700px] h-[40px] rounded bg-green-600 hover:bg-green-500 flex flex-col justify-center items-center self-center' onClick={()=>{if(file!==undefined){
            if(file.type==="application/pdf"){
              uploadFile()
            }
            }}}>
            <label className='inline text-white font-semibold cursor-pointer'><img className='w-4 h-4 inline mr-2' src={uploadIcon} alt="file"></img>Upload</label>
          </button>
        </div>
    </div>
  )
}
