import React from 'react';
import { Link } from "react-router-dom";

export default function Navbar() {
  return (
    <div className='absolute top-6 w-[90%] lg:w-[80%] h-[50px] flex-row justify-start items-center'>
        <ul className='flex flex-col lg:flex-row items-center gap-4 lg:gap-16 text-white font-semibold'>
            <div className='flex flex-row items-center justify-center lg:mr-[70px]'>
              <li className=''><Link to="/" className='h-[100%] inline'><img className="w-8 h-8" src={require("../Images/logo.png")}></img></Link></li>
              <li className='ml-4'><Link to="/" className='text-[22px] inline'>SPPU Result Converter</Link></li>
            </div>
            <li className='block'><Link to="/">Home</Link></li>
            <li className='block'><Link to="/about">About</Link></li>
            <li className='block'><Link to="/login">Login</Link></li>
        </ul>
    </div>
  )
}
