import React from 'react';
import { Link } from 'react-router-dom';
import registerIcon from "../Images/right-to-bracket-solid.svg";

export default function Register() {
  return (
    <div className='flex flex-col justify-center items-center w-[100%] mt-[40%] lg:mt-0' data-aos="fade-up" data-aos-duration="1000">
        <h1 className='text-white text-[28px] font-semibold mb-4'>Register</h1>
        <div className='border-[1px] border-gray-700 rounded h-[550px] w-[95%] lg:w-[27%]  pl-8 pt-10'>
            <div className='flex flex-col mb-4'>
                <label className='text-white font-semibold mb-2'>Name: </label>
                <input type="text" className='bg-[#27374a] w-[90%] h-[40px] rounded outline-none text-white pl-4' placeholder='Enter name'></input>
            </div>
            <div className='flex flex-col mb-4'>
                <label className='text-white font-semibold mb-2'>Username: </label>
                <input type="text" className='bg-[#27374a] w-[90%] h-[40px] rounded outline-none text-white pl-4' placeholder='Enter username'></input>
            </div>
            <div className='flex flex-col mb-4'>
                <label className='text-white font-semibold mb-2'>Email: </label>
                <input type="email" className='bg-[#27374a] w-[90%] h-[40px] rounded outline-none text-white pl-4' placeholder='Enter email'></input>
            </div>
            <div className='flex flex-col mb-6'>
                <label className='text-white font-semibold mb-2'>Password: </label>
                <input type="password" className='bg-[#27374a] w-[90%] h-[40px] rounded outline-none text-white pl-4' placeholder='Enter password'></input>
            </div>
            <div className='flex flex-col mb-6'>
                <label className='font-semibold text-white'>Already a User?</label>
                <Link to="/login" className='text-blue-600 font-semibold'>Login</Link>
            </div>
            <div className='flex flex-col'>
                <button className='w-[90%] h-[40px] rounded bg-green-600 hover:bg-green-500 flex flex-col justify-center items-center'>
                    <label className='inline text-white font-semibold cursor-pointer'><img className='w-4 h-4 inline mr-2' src={registerIcon} alt="file"></img>Register</label>
                </button>
            </div>
        </div>
    </div>
  )
}
